
       .layout-about {
           background-image: url(../images/info-bcg.png);
           background-size: contain;
           background-repeat: no-repeat;
           background-position: center;
           @media screen and (min-width: 768px) {
               background-image: none;
           }
       }
       
       .description {
           margin-bottom: 113px;
           padding-top: 26px;
           @media screen and (min-width: 768px) {
               padding-top: 50px;
               margin-bottom: 66px;
               display: flex;
           }
           @media screen and (min-width: 768px) {
               padding-top: 55px;
           }
       }
       
       .description__text {
           position: relative;
           padding: 0 30px;
           @media screen and (min-width: 768px) {
               width: 340px;
               padding: 0;
           }
           @media screen and (min-width: 1280px) {
               width: 463px;
           }
           &-item {
               padding: 23px 0;
               font-weight: 500;
               line-height: 19px;
               @media screen and (min-width: 768px) {
                   padding: 0;
                   padding-top: 35px;
                   line-height: 25px;
               }
               @media screen and (min-width: 1280px) {
                   margin-top: 100px;
               }
           }
           &-bacground {
               width: 100%;
               position: absolute;
               bottom: 0;
               left: -20px;
               max-width: 490px;
               @media screen and (min-width: 768px) {
                   display: none;
               }
           }
           &-img {
               width: calc(100% + 40px);
           }
       }
       
       .item--grey {
           color: $grey;
       }
       
       .item-text {
           margin-top: 20px;
       }
       
       .description__img {
           padding: 0 26px;
           width: 100%;
           height: 279px;
           display: flex;
           justify-content: center;
           @media screen and (min-width: 768px) {
               padding: 0;
               margin-right: 16px;
               width: 340px;
               height: 340px;
           }
           @media screen and (min-width: 1280px) {
               margin-right: 74px;
               width: 516px;
               height: 516px;
           }
           &-img {
               height: 100%;
               max-width: 280px;
               width: 100%;
               object-fit: contain;
               @media screen and (min-width: 768px) {
                   max-width: 340px;
               }
               @media screen and (min-width: 1280px) {
                   max-width: 516px;
               }
           }
       }
       
       .about__btn {
           display: flex;
           flex-direction: column;
           align-items: center;
           @media screen and (min-width: 768px) {
               margin-top: 62px;
               align-items: flex-start;
           }
       }
       
     
.button {
	@include btn(
		$width: 161px,
		$height: 44px,
		$color: $red,
		$font-weight: bold,
		$background: $white,
		$font-size: 16px,
		$line-height: 21px
	);
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

	&:hover {
		background-color: $grey-hover;
	}
}
       
       .button::after {
           content: "";
           background-image: url(../images/btn-icon.svg);
           display: block;
           margin-left: 16px;
           height: 10px;
           width: 8px;
           background-size: cover;
           background-repeat: no-repeat;
           background-position: center;
       }
       
       .layout-about {
           @media screen and (min-width: 1280px) {
               background-image: url(../images/info-bcg.png);
               background-position: center;
               background-repeat: no-repeat;
           }
       }
       
       .description__text-item.item--grey {
           @media screen and (min-width: 1280px) {
               margin-top: 35px;
           }
       }

